body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #303030;
  color: #FFFFFF;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1.4;
  background: #ffffff;
  color: #333333;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
}


.container {
 background-color: #fbd6b6;
}

.header .nav {
  display: flex;
  flex-direction: row;
  flex: 1;
  flex-basis: auto;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem 0;
  margin: 0 auto;
  background-color: #9d6f58;
  -webkit-box-shadow: 0px 16px 12px 0px rgba(64, 64, 64, 0.21);
-moz-box-shadow: 0px 16px 12px 0px rgba(64, 64, 64, 0.21);
box-shadow: 0px 16px 12px 0px rgba(64, 64, 64, 0.21);
}
.header .nav-logo {
  font-family: inherit;
  line-height: inherit;
}
.header .nav-logo__link {
  font-size: 1.7rem;
  font-weight: 700;
  color: #333333;
  padding-left: 20px;
}
.header .nav-menu__item {
  display: flex;
  flex-direction: row;
  flex: 1;
  flex-basis: auto;
}
.header .nav-menu__link {
  margin: 0 2rem 0 0;
  font-family: inherit;
  font-size: 1rem;
  font-weight: 500;
  color: #333333;
}
.header .nav-menu__link:hover {
  color: #006aff;
}
.header .hero {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0 auto;
  max-width: 68rem;
}
.header .hero-text__main {
  font-family: inherit;
  font-size: 2rem;
  font-weight: 700;
  line-height: inherit;
  text-transform: uppercase;
  color: #333333;
}
.header .hero-text__subs {
  font-family: inherit;
  font-size: 1rem;
  font-weight: 400;
  text-transform: capitalize;
  color: #333333;
  margin: 1rem 0 3rem 0;
}
.header .hero-text__btn {
  padding: 1rem 2rem;
  font-family: inherit;
  font-size: 1.2rem;
  font-weight: 500;
  text-align: center;
  color: #ffffff;
  background: #006aff;
  border: none;
  outline: none;
  border-radius: 2px;
}
.header .hero-image__main {
  width: 100%;
  height: auto;
  border-radius: 35%;
}



